<template>

    <div class="credit-order-wrapper">

        <div class="section-title section-title-row">

            <div class="section-title-left">
                <h1>Кредитная программа «{{currentOrder.name}}»</h1>
                <div class="credit-status" :class="orderStatus(currentOrder.status).status">{{orderStatus(currentOrder.status).title}}</div>
            </div>

            <div class="section-title-right">
                <a v-if="questionnaire.length > 0"
                   href="#"
                   class="download-link-2"
                   @click="downloadFile()"><span class="icon-download-3"></span>Скачать сформированную заявку</a>
            </div>

        </div>

        <div class="block">
            <div class="block-title">
                <div class="h2">Информация о кредите</div>
            </div>
            <div class="block-content">
                <div class="loans-issued-item no-hover">
                    <div class="load-issued-img">
                        <img :src="require('@/assets/images/dist/credit-img--1.png')" alt="">
                    </div>
                    <div class="loans-issued-title">
                        <div class="h4">Кредит «{{currentOrder.name}}»</div>
                        <p>{{currentOrder.purpose}}</p>
                    </div>
                    <div class="loans-price">Сумма <span>{{currentOrder.sum | numberFilter}}</span></div>
                    <div class="loans-conditions">
                        <ul>
                            <li>Ставка: <span>1.5%</span></li>
                            <li>Комиссия: <span>10 000 руб.</span></li>
                            <li>Страхование жизни: <span>Да</span></li>
                            <li>Срок: <span>{{currentOrder.term}} {{$options.filters.declensionNumbers(currentOrder.term, ['месяц', 'месяца', 'месяцев'])}}</span></li>
                            <li>Дата создания заявки: <span>{{parseDate(currentOrder.created_at)}}</span></li>
                        </ul>
                    </div>
                </div>
            </div>
        </div>

        <div class="block">
            <div class="block-title">
                <div class="h2">Документы</div>
            </div>
            <div class="block-content">
                <div class="documents-list">
                    <ul>
                        <li v-for="doc in currentOrderDocs"
                            :key="doc.id">

                            <div class="document-item">
                                <div class="document-item-title">
                                    {{doc.name}}
                                </div>
                                <div class="document-files-list">
                                    <div v-for="file in doc.files"
                                         :key="file.id"
                                         class="files-item">
                                        <span class="document-link"
                                              @click="downloadFile(file)">
                                            <span class="icon-document"></span>
                                            <span class="document-link-main">
                                                <span class="document-link-title">{{file.name}}.{{file.format}}</span>
                                                <span class="document-link-size">({{convertSize(file.size)}})</span>
                                            </span>
                                        </span>
                                    </div>
                                </div>
                            </div>

                        </li>
                    </ul>
                </div>
            </div>
        </div>

<!--        <DocView v-if="checkDocs"-->
<!--                 :list="checkDocsViews"-->
<!--                 :familiarizedBtn="false"-->
<!--                 @close-modal="closeViewDocModal"/>-->

    </div>

</template>

<script>

    // import DocView from "@/components/Blocks/DocView";
    import {mapState} from 'vuex'
    import moment from "moment";

    export default {
        components: {
            // DocView
        },
        computed: {
          ...mapState({
              currentOrder: state => state.createOrder.orderFullInfo.order,
              questionnaire: state => state.createOrder.neededToLoadFiles,
              currentOrderDocs: state => state.orders.orderDocs
          })
        },
        methods: {
            orderStatus(id) {
                switch (id) {
                    case 1:
                        return {
                            title: 'Новая заявка',
                            status: 'secondary'
                        }
                    case 2:
                        return {
                            title: 'Идет обработка данных',
                            status: 'secondary'
                        }
                    case 3:
                        return {
                            title: 'Ошибка отправки в диадок',
                            status: 'danger'
                        }
                    case 4:
                        return {
                            title: 'Отправлен в диадок',
                            status: 'secondary'
                        }
                    case 5:
                        return {
                            title: 'Ошибка отправки в банк',
                            status: 'danger'
                        }
                    case 6:
                        return {
                            title: 'Рассматривается банком',
                            status: 'secondary'
                        }
                    case 7:
                        return {
                            title: 'Отклонен',
                            status: 'danger'
                        }
                    case 8:
                        return {
                            title: 'Дополнительные документы',
                            status: 'secondary'
                        }
                    case 9:
                        return {
                            title: 'Одобрен',
                            status: 'secondary'
                        }
                    case 10:
                        return {
                            title: 'Отклонен пользователем',
                            status: 'danger'
                        }
                    case 11:
                        return {
                            title: 'Резервирование расчетного счета',
                            status: 'warning'
                        }
                    case 12:
                        return {
                            title: 'Есть зарезервированный расчетный счет',
                            status: 'secondary'
                        }
                    case 13:
                        return {
                            title: 'Подготовка документов',
                            status: 'warning'
                        }
                    case 14:
                        return {
                            title: 'Подписание',
                            status: 'success'
                        }
                    case 15:
                        return {
                            title: 'Идет обработка данных',
                            status: 'secondary'
                        }
                    case 16:
                        return {
                            title: 'Ошибка отправки в диадок',
                            status: 'danger'
                        }
                    case 17:
                        return {
                            title: 'Отправлен в диадок',
                            status: 'success'
                        }
                    case 18:
                        return {
                            title: 'Ошибка отправки в банк',
                            status: 'danger'
                        }
                    case 19:
                        return {
                            title: 'Проверка документов',
                            status: 'warning'
                        }
                    case 20:
                        return {
                            title: 'Кредит выдан',
                            status: 'success'
                        }
                    default:
                        return {
                            title: '',
                            status: ''
                        }
                }
            },
            convertSize(size) {
                if ( size > 999 && size < 1000000) {
                    return `${(size / 1000).toFixed(2)}КБ`
                }
                else if ( size > 999999 && 1000000000 ) {
                    return `${(size / 1000000).toFixed(2)}МБ`
                }
                else {
                    return `${size.toFixed(2)}Б`
                }
            },
            openDocViewer(doc) {
                let docObject = {}
                docObject.id = doc.id
                docObject.title = doc.title
                docObject.list = []
                doc.docs.forEach(item => {
                    docObject.list.push(item)
                })
                this.checkDocsViews = []
                this.checkDocsViews.push(docObject)
                this.checkDocs = true
            },
            closeViewDocModal() {
                this.checkDocsViews = []
                this.checkDocs = false
            },
            parseDate(date) {
                return moment(date).format('DD.MM.YYYY')
            },
            downloadFile(file) {
                this.$store.dispatch('getFile', file.id).then(content => {
                    this.$store.dispatch('downloadFile', {content, name: file.name, format: file.format})
                })
            }
        },
        mounted() {
            let id = this.$route.params.id
            this.$store.dispatch('getCurrentOrder', id)
            this.$store.dispatch('getDetailOrderDocs', id)
        }
    }
</script>
